  margin: 0 auto;
  padding: 95px 0 50px 0;
  max-width: 1160px;
}
@media (max-width: 1199px) {
  .stocks__wrapper {
    padding: 92px 0 50px 0;
  }
}
@media (max-width: 767px) {
  .stocks__wrapper {
    padding: 64px 0 40px 0;
  }
}

.stocks__wrapper h2 {
  margin: 0 0 34px 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 54px;
  line-height: 64.8px;
  letter-spacing: 1.7px;
  text-align: center;
}
@media (max-width: 1199px) {
  .stocks__wrapper h2 {
    margin: 0 8px 40px 0;
    font-size: 42px;
    line-height: 50.4px;
    letter-spacing: 1.3px;
  }
}
@media (max-width: 767px) {
  .stocks__wrapper h2 {
    margin: 0 5px 42px 0;
    font-size: 32px;
    line-height: 38.4px;
    letter-spacing: 1px;
  }
}

.stocks__wrapper ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .stocks__wrapper ul {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .stocks__wrapper ul {
    grid-template-columns: 1fr;
  }
}

.stocks__wrapper li {
  max-width: 560px;
}
@media (max-width: 1199px) {
  .stocks__wrapper li {
    max-width: 100%;
    min-height: 211px;
  }
}

.stocks__wrapper a {
  user-select: none;
}
@media (hover: hover) {
  .stocks__wrapper a:hover:not(.focus-visible) {
    transform: scale(1.1);
    transition: 0.3s;
  }
}
.stocks__wrapper a:active {
  opacity: 50%;
}

.stocks__main-offer {
  grid-column: 1/2;
  grid-row: 1/3;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 1199px) {
  .stocks__main-offer {
    grid-column: 1/3;
    grid-row: 1/2;
    grid-template-columns: 1fr auto;
  }
}
@media (min-width: 880px) and (max-width: 1199px) {
  .stocks__main-offer {
    grid-template-columns: 52% 48%;
  }
}
@media (max-width: 767px) and (max-width: 1199px) {
  .stocks__main-offer {
    grid-column: 1/2;
    grid-template-columns: 1fr;
  }
}

.stocks__main-offer-content-wrapper {
  display: flex;
  flex-direction: column;
  padding: 34px 45px 36px 45px;
  background-color: #ed0233;
  color: #ffffff;
}
@media (max-width: 767px) {
  .stocks__main-offer-content-wrapper {
    padding: 34px 45px 37px 45px;
    text-align: center;
  }
}

.stocks__main-offer-content-wrapper h3 {
  margin: 0;
  font-weight: 700;
  font-size: 28px;
  line-height: 43.7px;
}

.stocks__main-offer-content-wrapper span {
  display: block;
  margin: -11px 0 14px 0;
  font-weight: 700;
  font-size: 70px;
  line-height: 80px;
}
@media (max-width: 767px) {
  .stocks__main-offer-content-wrapper span {
    margin: -11px 0 8px 0;
  }
}

.stocks__main-offer-content-wrapper p {
  flex-grow: 1;
  margin: 0;
  font-size: 18px;
  line-height: 25.2px;
}
@media (max-width: 767px) {
  .stocks__main-offer-content-wrapper p {
    margin: 0 0 20px 0;
  }
}

.stocks__main-offer-content-wrapper a {
  position: relative;
  margin: 0 0 0 -10px;
  padding: 10px;
  width: max-content;
  font-weight: 700;
  font-size: 14px;
  line-height: 16.41px;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  color: #ffffff;
}
.stocks__main-offer-content-wrapper a::before {
  content: "";
  position: absolute;
  bottom: 8px;
  left: 10px;
  width: calc(100% - 20px);
  height: 1px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .stocks__main-offer-content-wrapper a {
    margin: 0 auto;
  }
}

.stocks__main-offer-image-wrapper {
  width: 100%;
  height: 100%;
  background-color: #4470df;
}
@media (max-width: 1199px) {
  .stocks__main-offer-image-wrapper {
    min-width: 399px;
    min-height: 462px;
    aspect-ratio: 399/462;
  }
}
@media (max-width: 767px) {
  .stocks__main-offer-image-wrapper {
    width: 100%;
    min-width: 290px;
    min-height: 299px;
    aspect-ratio: auto;
  }
}

.stocks__main-offer-image-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.stocks__secondary-offer {
  display: flex;
  flex-direction: column;
  padding: 23px 28px 20px 28px;
  min-height: 211px;
  border: 7px solid;
  border-image: linear-gradient(148.82deg, #d10a42 0%, #00359f 100%);
  border-image-slice: 1;
}
@media (max-width: 1199px) {
  .stocks__secondary-offer {
    padding: 23px 28px 20px 28px;
  }
}
@media (max-width: 767px) {
  .stocks__secondary-offer {
    padding: 34px 28px 30px 28px;
    text-align: center;
  }
}

.stocks__secondary-offer h3 {
  margin: 0 0 6px 0;
  font-weight: 700;
  font-size: 26px;
  line-height: 30px;
}
@media (max-width: 767px) {
  .stocks__secondary-offer h3 {
    margin: 0 0 8px 0;
  }
}

.stocks__secondary-offer p {
  flex-grow: 1;
  margin: 0 0 20px 0;
}
@media (max-width: 1199px) {
  .stocks__secondary-offer p {
    line-height: 23px;
  }
}
@media (max-width: 767px) {
  .stocks__secondary-offer p {
    flex-grow: 0;
    margin: 0 0 16px 0;
    line-height: 22px;
  }
}
@media (min-width: 340px) {
  .stocks__secondary-offer p {
    flex-grow: 1;
  }
}

@media (min-width: 780px) {
  .stocks__secondary-offer br {
    display: none;
  }
}
@media (min-width: 350px) and (max-width: 767px) {
  .stocks__secondary-offer br {
    display: none;
  }
}

.stocks__secondary-offer a {
  position: relative;
  margin: 0 0 0 -10px;
  padding: 10px;
  width: max-content;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  color: #1c3374;
}
.stocks__secondary-offer a::before {
  content: "";
  position: absolute;
  bottom: 8px;
  left: 10px;
  width: calc(100% - 20px);
  height: 1px;
  background-color: #1c3374;
}
@media (max-width: 767px) {
  .stocks__secondary-offer a {
    margin: 0 auto;
  }
}

